@font-face {
  font-family: BPDots;
  src: url("BPdotsSquareVF.25fec6ee.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: ZxGamut;
  src: url("ZxGamut-Variable.6528b92f.woff2") format("woff2");
  font-display: swap;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background: #171717;
  min-height: 100vh;
  font-family: ZxGamut, monospace;
  overflow-x: hidden;
}

.app {
  z-index: 10;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
  display: flex;
  position: relative;
  overflow-y: auto;
}

#p5-background {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.landing-container {
  text-align: center;
  backdrop-filter: blur(20px) saturate(1.8);
  background: #171717a8;
  border: 1px solid #ffffff1a;
  border-radius: 15px;
  width: 100%;
  max-width: 900px;
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px #0000004d;
}

.hero-section {
  border-bottom: 1px solid #ffffff1a;
  padding: 3rem;
}

.hero-text {
  color: #f0f0f0;
  margin: 2rem 0;
  font-size: 1.3rem;
}

.content-sections {
  padding: 0 1rem 1rem;
}

.content-section {
  text-align: left;
  border-bottom: 1px solid #ffffff0d;
  padding: 2rem 2rem 1.5rem;
}

.content-section:last-child {
  border-bottom: none;
}

.program-details {
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 0;
  display: grid;
}

.detail-item {
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 8px;
  padding: 1rem;
}

.curriculum-grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 0;
  display: grid;
}

.curriculum-block {
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 10px;
  padding: 1.5rem;
}

.curriculum-block h3 {
  color: #4ecdc4;
  margin-bottom: .5rem;
  font-family: BPDots, monospace;
  font-size: 1.1rem;
  font-weight: 900;
}

.who-list {
  margin: 1rem 0 0;
  padding: 0;
  list-style: none;
}

.who-list li {
  margin-bottom: .5rem;
  padding: .5rem 0 .5rem 1.5rem;
  position: relative;
}

.who-list li:before {
  content: "→";
  color: #4ecdc4;
  font-size: 1.2rem;
  font-weight: bold;
  position: absolute;
  left: 0;
}

.diversity-note {
  color: #ccc;
  border-top: 1px solid #ffffff1a;
  margin-top: 1.5rem;
  padding: 1.5rem 0 1rem;
  font-style: italic;
}

.team-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 1rem;
  margin-bottom: 0;
  display: grid;
}

.team-member {
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 10px;
  padding: 1.5rem;
}

.team-member h3 {
  color: #fff;
  margin-bottom: .75rem;
  font-family: BPDots, monospace;
  font-size: 1.2rem;
  font-weight: 900;
}

.contact-section {
  text-align: center;
  background: #4ecdc41a;
  border-radius: 8px;
  margin: 2rem;
  padding: 2rem 0;
  border: 1px solid #4ecdc44d !important;
}

.contact-section a {
  color: #4ecdc4;
  font-weight: bold;
  text-decoration: none;
}

.contact-section a:hover {
  color: #fff;
  text-decoration: underline;
}

h1 {
  color: #fff;
  text-shadow: 0 0 20px #ffffff80;
  margin-bottom: 1rem;
  font-family: BPDots, monospace;
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  color: #ccc;
  margin-bottom: 2rem;
  font-family: BPDots, monospace;
  font-size: 2rem;
  font-weight: 900;
}

p {
  color: #e0e0e0;
  margin-bottom: 1rem;
  font-family: ZxGamut, monospace;
  font-size: 1.2rem;
  line-height: 1.6;
}

p:last-child {
  margin-bottom: 0;
}

.cta-button {
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: linear-gradient(45deg, #1a3a5c, #2d5aa0);
  border: none;
  border-radius: 5px;
  padding: 1rem 2rem;
  font-family: BPDots, monospace;
  font-size: 1.2rem;
  font-weight: 900;
  transition: transform .3s, box-shadow .3s;
}

.cta-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px #1a3a5c66;
}

@media (width <= 768px) {
  .app {
    padding: 1rem;
  }

  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  p {
    font-size: 1rem;
  }

  .hero-section {
    padding: 1.5rem;
  }

  .content-section {
    padding: 1rem;
  }

  .program-details, .curriculum-grid, .team-grid {
    grid-template-columns: 1fr;
  }

  .contact-section {
    margin: 1rem;
  }
}
